body {
	margin: 0;
	padding: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.column {
	display: flex;
	flex-flow: column wrap;
}

.right {
	margin-left: auto;
	margin-right: 10px;
}

.pointer {
	cursor: pointer;
}

#footer {
	position: fixed;
	bottom: 0;
	width: 100%;
	height: 60px;
	line-height: 60px;
	background-color: #f5f5f5;
	clear: both;
}

#sidebar {
	overflow: auto;
	padding-bottom: 100px;
	height: 100vh;
}

.my-mt-5 {
	margin-top: 5.5rem!important;
}
.input-tags {
	background-color: #fff;
	border: 1px solid #ccc;
	box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
	display: inline-block;
	color: #555;
	vertical-align: middle;
	border-radius: 4px;
	max-width: 100%;
	cursor: text;
	width: 100%;
	height: calc(2.25rem + 2px);
	padding: .375rem .75rem;
	font-size: 1rem;
	line-height: 1.5;
	margin-top: 5px;
}

.input-tags>input {
	border: none;
	box-shadow: none;
	outline: none;
	background-color: transparent;
	padding: 0 6px;
	margin: 0;
	width: auto;
	max-width: inherit;
}

.input-tags:focus-within {
	color: #495057;
	background-color: #fff;
	border-color: #80bdff;
	outline: 0;
	box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}

span.tag {
	margin-right: 2px;
	color: white;
	background-color: #5bc0de;
	border-radius: .25em;
	padding: .2em .6em .3em;
	font-size: 95%;
	line-height: 1;
}

span.remove {
	margin-left: 8px;
	cursor: pointer;
}

span.remove::after {
	content: "x";
	padding: 0px 2px;
}

.tags .auto-complete {
	margin-top: 5px;
	max-height: 60px;
	overflow-y: scroll;
}

.tags .auto-complete::before {
	white-space: pre-line;
}

.item-suggested.selected {
	background-color: skyblue;
	cursor: pointer;
}
